import { FC } from 'react';
import * as microsoftTeams from '@microsoft/teams-js';

import './App.css';

type AppProps = {};

export const App: FC<AppProps> = ({}) => {
    const handleTest = (ev: any) => {
        console.log('HERE BABY!!', ev);

        microsoftTeams.initialize();

        microsoftTeams.settings.registerOnSaveHandler((saveEvent) => {
            microsoftTeams.settings.setSettings({
                websiteUrl: 'https://lyubosumaz.github.io/react-portfolio-deploy/',
                contentUrl: 'https://lyubosumaz.github.io/react-portfolio-deploy/',
                entityId: 'omg-1',
                suggestedDisplayName: 'omg',
            });
            saveEvent.notifySuccess();
        });

        microsoftTeams.settings.setValidityState(true);
    };

    return (
        <div className="App">
            <header className="App-header">
                <button onClick={handleTest}>Omg bruh</button>
                <p>Omg Testing</p>
                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Magni quis assumenda porro tempora harum voluptatem velit vero. Nulla minus aspernatur, quasi cupiditate quaerat laudantium accusamus voluptates deserunt sunt eligendi numquam.</p>
                <p>1</p>
            </header>
        </div>
    );
};
